//()
function onSendMsg(msg,callback) {
    console.log('accountMode',accountMode);
    if (!loginInfo.identifier) {//
        if (accountMode == 1) {//
            //account_typecookie,1
            webim.Tool.setCookie('accountType', loginInfo.accountType, 3600 * 24);
            //tls
            tlsLogin();
        } else {//
            console.error('');
        }
        return;
    }
    
    if (!selToID) {
        console.error("");
        return;
    }
    //
    var msgtosend = msg;
    var msgLen = webim.Tool.getStrBytes(msg);

    if (msgtosend.length < 1) {
        console.error("!");
        return;
    }

    var maxLen, errInfo;
    if (selType == webim.SESSION_TYPE.GROUP) {
        maxLen = webim.MSG_MAX_LENGTH.GROUP;
        errInfo = "(" + Math.round(maxLen / 3) + ")";
    } else {
        maxLen = webim.MSG_MAX_LENGTH.C2C;
        errInfo = "(" + Math.round(maxLen / 3) + ")";
    }
    if (msgLen > maxLen) {
        console.error(errInfo);
        return;
    }

    if (!selSess) {
        selSess = new webim.Session(selType, selToID, selToID, selSessHeadUrl, Math.round(new Date().getTime() / 1000));
    }
    var isSend = true;//
    var seq = -1;//-1sdk
    var random = Math.round(Math.random() * 4294967296);//
    var msgTime = Math.round(new Date().getTime() / 1000);//
    var subType;//
    if (selType == webim.SESSION_TYPE.GROUP) {
        //
        //webim.GROUP_MSG_SUB_TYPE.COMMON-,
        //webim.GROUP_MSG_SUB_TYPE.LOVEMSG-
        //webim.GROUP_MSG_SUB_TYPE.TIP-()
        //webim.GROUP_MSG_SUB_TYPE.REDPACKET-
        subType = webim.GROUP_MSG_SUB_TYPE.COMMON;

    } else {
        //C2C
        //webim.C2C_MSG_SUB_TYPE.COMMON-,
        subType = webim.C2C_MSG_SUB_TYPE.COMMON;
    }
    var msg = new webim.Msg(selSess, isSend, seq, random, msgTime, loginInfo.identifier, subType, loginInfo.identifierNick);
    //
    var expr = /\[[^[\]]{1,3}\]/mg;
    var emotions = msgtosend.match(expr);
    var text_obj, face_obj, tmsg, emotionIndex, emotion, restMsgIndex;
    if (!emotions || emotions.length < 1) {
        text_obj = new webim.Msg.Elem.Text(msgtosend);
        msg.addText(text_obj);
    } else {//

        for (var i = 0; i < emotions.length; i++) {
            tmsg = msgtosend.substring(0, msgtosend.indexOf(emotions[i]));
            if (tmsg) {
                text_obj = new webim.Msg.Elem.Text(tmsg);
                msg.addText(text_obj);
            }
            emotionIndex = webim.EmotionDataIndexs[emotions[i]];
            emotion = webim.Emotions[emotionIndex];
            if (emotion) {
                face_obj = new webim.Msg.Elem.Face(emotionIndex, emotions[i]);
                msg.addFace(face_obj);
            } else {
                text_obj = new webim.Msg.Elem.Text(emotions[i]);
                msg.addText(text_obj);
            }
            restMsgIndex = msgtosend.indexOf(emotions[i]) + emotions[i].length;
            msgtosend = msgtosend.substring(restMsgIndex);
        }
        if (msgtosend) {
            text_obj = new webim.Msg.Elem.Text(msgtosend);
            msg.addText(text_obj);
        }
    }

    webim.sendMsg(msg, function (resp) {
        if (selType == webim.SESSION_TYPE.C2C) {//
            showMsg(msg);
        }
        webim.Log.info("");
        callback && callback();

        //hideDiscussForm();//
        //showDiscussTool();//
        //hideDiscussEmotion();//
    }, function (err) {
        webim.Log.error(":" + err.ErrorInfo);
        console.error(":" + err.ErrorInfo);
    });
}

function sendC2CCustomMsg(toUserID, msg, callback) {
    if (!loginInfo.identifier) {//
        if (accountMode == 1) {//
            //account_typecookie,1
            webim.Tool.setCookie('accountType', loginInfo.accountType, 3600 * 24);
            //tls
            tlsLogin();
        } else {//
            console.error('');
        }
        return;
    } 

   // custom
   var data = msg.data || '';
   var desc = msg.desc || '';
   var ext = msg.ext || '';

   var msgLen = webim.Tool.getStrBytes(data); 

   var maxLen = webim.MSG_MAX_LENGTH.C2C;
   var errInfo = "(" + Math.round(maxLen / 3) + ")";
   if (msgLen > maxLen) {
        alert(errInfo);
        return;
    }

    var session = new webim.Session(webim.SESSION_TYPE.C2C, toUserID, toUserID, '', Math.round(new Date().getTime() / 1000));
    var isSend = true;//
    var seq = -1;//-1sdk
    var random = Math.round(Math.random() * 4294967296);//
    var msgTime = Math.round(new Date().getTime() / 1000);//
    var subType = webim.C2C_MSG_SUB_TYPE.COMMON;// 
    
    var msg = new webim.Msg(session, isSend, seq, random, msgTime, loginInfo.identifier, subType, loginInfo.identifierNick);

    var custom_obj = new webim.Msg.Elem.Custom(data, desc, ext);
    msg.addCustom(custom_obj);

    //
    webim.sendMsg(msg, function (resp) {
        webim.Log.info("");
        console.log('');
        callback && callback({
            errCode: 0,
            errMsg: ""
        });
    }, function (err) {
        webim.Log.info(err.ErrorInfo);
        console.log(':', err);
        callback && callback({
            errCode: -1,
            errMsg: ':' + err.ErrorInfo
        });
    });
}

/**
 * 
 * custom+text
 * text
 */
function sendCustomMsg(msg,callback) {

    if (!loginInfo.identifier) {//
        if (accountMode == 1) {//
            //account_typecookie,1
            webim.Tool.setCookie('accountType', loginInfo.accountType, 3600 * 24);
            //tls
            tlsLogin();
        } else {//
            console.error('');
        }
        return;
    }

    if (!selToID) {
        console.error("");
        return;
    }
    
    // custom
    var data = msg.data || '';
    var desc = msg.desc || '';
    var ext = msg.ext || '';

    var msgLen = webim.Tool.getStrBytes(data);

    if (data.length < 1) {
        alert("!");
        return;
    }
    var maxLen, errInfo;
    if (selType == webim.SESSION_TYPE.C2C) {
        maxLen = webim.MSG_MAX_LENGTH.C2C;
        errInfo = "(" + Math.round(maxLen / 3) + ")";
    } else {
        maxLen = webim.MSG_MAX_LENGTH.GROUP;
        errInfo = "(" + Math.round(maxLen / 3) + ")";
    }
    if (msgLen > maxLen) {
        alert(errInfo);
        return;
    }

    // text
    var msgtosend = msg.text;
    var msgLen = webim.Tool.getStrBytes(msg.text);

    if (msgtosend.length < 1) {
        console.error("!");
        return;
    }

    var maxLen, errInfo;
    if (selType == webim.SESSION_TYPE.GROUP) {
        maxLen = webim.MSG_MAX_LENGTH.GROUP;
        errInfo = "(" + Math.round(maxLen / 3) + ")";
    } else {
        maxLen = webim.MSG_MAX_LENGTH.C2C;
        errInfo = "(" + Math.round(maxLen / 3) + ")";
    }
    if (msgLen > maxLen) {
        console.error(errInfo);
        return;
    }

    if (!selSess) {
        selSess = new webim.Session(selType, selToID, selToID, selSessHeadUrl, Math.round(new Date().getTime() / 1000));
    }
    var isSend = true;//
    var seq = -1;//-1sdk
    var random = Math.round(Math.random() * 4294967296);//
    var msgTime = Math.round(new Date().getTime() / 1000);//
    var subType;//
    if (selType == webim.SESSION_TYPE.GROUP) {
        //
        //webim.GROUP_MSG_SUB_TYPE.COMMON-,
        //webim.GROUP_MSG_SUB_TYPE.LOVEMSG-
        //webim.GROUP_MSG_SUB_TYPE.TIP-()
        //webim.GROUP_MSG_SUB_TYPE.REDPACKET-
        subType = webim.GROUP_MSG_SUB_TYPE.COMMON;

    } else {
        //C2C
        //webim.C2C_MSG_SUB_TYPE.COMMON-,
        subType = webim.C2C_MSG_SUB_TYPE.COMMON;
    }
    var msg = new webim.Msg(selSess, isSend, seq, random, msgTime, loginInfo.identifier, subType, loginInfo.identifierNick);
    
    var custom_obj = new webim.Msg.Elem.Custom(data, desc, ext);
    msg.addCustom(custom_obj);

    //
    var expr = /\[[^[\]]{1,3}\]/mg;
    var emotions = msgtosend.match(expr);
    var text_obj, face_obj, tmsg, emotionIndex, emotion, restMsgIndex;
    if (!emotions || emotions.length < 1) {
        text_obj = new webim.Msg.Elem.Text(msgtosend);
        msg.addText(text_obj);
    } else {//
        for (var i = 0; i < emotions.length; i++) {
            tmsg = msgtosend.substring(0, msgtosend.indexOf(emotions[i]));
            if (tmsg) {
                text_obj = new webim.Msg.Elem.Text(tmsg);
                msg.addText(text_obj);
            }
            emotionIndex = webim.EmotionDataIndexs[emotions[i]];
            emotion = webim.Emotions[emotionIndex];
            if (emotion) {
                face_obj = new webim.Msg.Elem.Face(emotionIndex, emotions[i]);
                msg.addFace(face_obj);
            } else {
                text_obj = new webim.Msg.Elem.Text(emotions[i]);
                msg.addText(text_obj);
            }
            restMsgIndex = msgtosend.indexOf(emotions[i]) + emotions[i].length;
            msgtosend = msgtosend.substring(restMsgIndex);
        }
        if (msgtosend) {
            text_obj = new webim.Msg.Elem.Text(msgtosend);
            msg.addText(text_obj);
        }
    }
    //
    webim.sendMsg(msg, function (resp) {
        if(selType==webim.SESSION_TYPE.C2C){//
            addMsg(msg);
        }
        webim.Log.info("");
        console.log('');
        callback && callback();
    }, function (err) {
        webim.Log.info(err.ErrorInfo);
        console.log(':',err);
    });
}

//()
function sendGroupLoveMsg() {

    if (!loginInfo.identifier) {//
        if (accountMode == 1) {//
            //account_typecookie,1
            webim.Tool.setCookie('accountType', loginInfo.accountType, 3600 * 24);
            //tls
            tlsLogin();
        } else {//
            console.error('');
        }
        return;
    }

    if (!selToID) {
        console.error("");
        return;
    }

    if (!selSess) {
        selSess = new webim.Session(selType, selToID, selToID, selSessHeadUrl, Math.round(new Date().getTime() / 1000));
    }
    var isSend = true;//
    var seq = -1;//-1sdk
    var random = Math.round(Math.random() * 4294967296);//
    var msgTime = Math.round(new Date().getTime() / 1000);//
    //
    //webim.GROUP_MSG_SUB_TYPE.COMMON-,
    //webim.GROUP_MSG_SUB_TYPE.LOVEMSG-
    //webim.GROUP_MSG_SUB_TYPE.TIP-()
    //webim.GROUP_MSG_SUB_TYPE.REDPACKET-
    var subType = webim.GROUP_MSG_SUB_TYPE.LOVEMSG;

    var msg = new webim.Msg(selSess, isSend, seq, random, msgTime, loginInfo.identifier, subType, loginInfo.identifierNick);
    var msgtosend = 'love_msg';
    var text_obj = new webim.Msg.Elem.Text(msgtosend);
    msg.addText(text_obj);

    webim.sendMsg(msg, function (resp) {
        if (selType == webim.SESSION_TYPE.C2C) {//
            showMsg(msg);
        }
        webim.Log.info("");
    }, function (err) {
        webim.Log.error(":" + err.ErrorInfo);
        console.error(":" + err.ErrorInfo);
    });
}
