import LibGenerateTestUserSig from './lib-generate-test-usersig-es.min.js';

/**
 *  SDKAppId SDKAppId
 *
 * [](https://console.cloud.tencent.com/rav )  SDKAppId
 * 
 */
var SDKAPPID = 0;


/**
 * 
 * <p>
 * 
 * 7 x 24 x 60 x 60 = 604800 = 7 
 */
var EXPIRETIME = 604800;


/**
 * 
 *
 * step1. [](https://console.cloud.tencent.com/rav )
 * step2. 
 * step3.  UserSig 
 *
 * Demo UserSig 
 * https://cloud.tencent.com/document/product/647/17275#Server
 */
var SECRETKEY = "";

/*
 * Module:   GenerateTestUserSig
 *
 * Function:  UserSigUserSig 
 *            SDKAppIDUserID  EXPIRETIME  HMAC-SHA256
 *
 * Attention:  App 
 *
 *             UserSig SDK 
 *             SECRETKEY  Web 
 *             UserSig 
 *
 *             UserSig  App  UserSig
 *             App
 *
 * Referencehttps://cloud.tencent.com/document/product/647/17275#Server
 */
function genTestUserSig(userID) {
  
  var generator = new LibGenerateTestUserSig(SDKAPPID, SECRETKEY, EXPIRETIME);
  var userSig = generator.genTestUserSig(userID);

  return {
    sdkAppID: SDKAPPID,
    userSig: userSig
  };
}

module.exports = {
  genTestUserSig
}